.App {
  text-align: center;
}

.App-header {
  background-color: #919ab4;
  height: 90px;
  max-width: 70vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: min(20px, 3vw);
  font-family: Impact;
  color: #000;
  /* centers the div */
  margin: 0 auto;
  padding: 0 2rem;
  border-color: 0 solid #000;
  border-width: 0 0.4rem 0.4rem 0.5rem;
  border-style: solid;
  border-radius: 0 0 1rem 1rem;
}

.App-logo {
  height: 100%;
}
.App-logo:hover {
  /* spin once then stop */
  animation: App-logo-spin infinite 0.4s;

}

.App-button {
  background-color: #919ab4;
  border: 0.2rem solid #000;
  border-radius: 2rem;
  font-size: 20px;
  padding: .75rem;
  font-family: Impact;
}

.App-button:hover{
  background-color: #000;
  color: #919ab4;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin 1s;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
